import vis, {DataSet, Network} from 'vis'
import lazy from 'imlazy'
import titleCase from 'title-case'
import dijkstra from 'node-dijkstra'

import * as store from './store'
import * as config from './config'
import * as utils from './utils'

const $network = document.getElementById('network')
const $dijkstraBtn = document.getElementById('dijkstra-btn')

const colors = {
  person: '#95a5a6',
  edge: '#95a5a6',
  year: '#2ecc71',
  publicationType: '#34495e',
  dijkstra: '#9b59b6'
}

let nodes = new DataSet([])
let edges = new DataSet([])
let options = {}
let network = new Network($network, {nodes, edges}, options)

let dijkstraMode = false
let nodesForDijkstra = new Set()
let itensToResetForDijkstra = {nodes: [], edges: []}

let uniqueRelations = new Set()

$dijkstraBtn.addEventListener('click', prepareForDijkstra)
network.on('click', onClick)

export function init() {
  redraw()
}

export function redraw() {
  const faceId = config.get('face')

  if (faceId !== 0) {
    $dijkstraBtn.setAttribute('disabled', true)
  } else if (!dijkstraMode) {
    $dijkstraBtn.removeAttribute('disabled')
  }

  const face = ({
    0: firstFace,
    1: secondFace,
    2: thirdFace,
  })[faceId]()
}

export function clearNetwork() {
  nodes.clear()
  edges.clear()
  uniqueRelations = new Set()
}

function firstFace() {
  const state = store.data()

  getPeople(state)

  for (let production of state.productions.values()) {
    const authors = config.get('ppgccOnly')
      ? Array.from(
          lazy.filter(
            author => author.isPpgcc,
            production.authors.values()
          )
        )
      : Array.from(production.authors.values())

    for (let i = 0, len = authors.length; i < len; i++) {
      const author = authors[i]

      for (var j = i + 1, len = authors.length; j < len; j++) {
        const otherAuthor = authors[j]
        const edgeId = buildEdgeId(author, otherAuthor)

        if (!edges.get(edgeId)) {
          edges.add({
            id: edgeId,
            from: author.id,
            to: otherAuthor.id,
            // color: colors.edge,
          })
        }

        const edge = edges.get(edgeId)
        const relationKey = `rel:${author.id}:${otherAuthor.id}:${production.id}`

        if (!uniqueRelations.has(relationKey)) {
          uniqueRelations.add(relationKey)

          const newValue = (+edge.label || 0) + 1
          edges.update({
            id: edgeId,
            label: `${newValue}`,
            value: newValue,
            // color: colors.edge,
          })
          incrementValue(edges, edges.get(edgeId))

          incrementValue(nodes, nodes.get(author.id))
          incrementValue(nodes, nodes.get(otherAuthor.id))
        }
      }
    }
  }

  network.setOptions({
    nodes: {
      shape: 'dot',
      size: 16,
    },

    edges: {
      color: {
        color: colors.edge,
        highlight: colors.edge,
        hover: colors.edge,
      },
    },

    physics: {
      forceAtlas2Based: {
        gravitationalConstant: -300,
        centralGravity: 0.005,
        springLength: 230,
        springConstant: 0.18
      },
      maxVelocity: 146,
      solver: 'forceAtlas2Based',
      timestep: 0.35,
      stabilization: {iterations: 0}
    }
  })
}

function secondFace() {
  const state = store.data()

  getPeople(state)

  function buildEdgeId({id: idA}, {id: idB}) {
    if (idA > idB) {
      return `edge:${idA}:${idB}`
    }
    return `edge:${idB}:${idA}`
  }

  const allEdges = new DataSet()

  for (let production of state.productions.values()) {
    const authors = config.get('ppgccOnly')
      ? Array.from(
          lazy.filter(
            author => author.isPpgcc,
            production.authors.values()
          )
        )
      : Array.from(production.authors.values())

    for (let i = 0, len = authors.length; i < len; i++) {
      const author = authors[i]

      for (var j = i + 1, len = authors.length; j < len; j++) {
        const otherAuthor = authors[j]
        const edgeId = buildEdgeId(author, otherAuthor)

        if (!allEdges.get(edgeId)) {
          allEdges.add({
            id: edgeId,
            from: author.id,
            to: otherAuthor.id,
            // color: colors.edge,
          })
        }

        const edge = allEdges.get(edgeId)
        const relationKey = `rel:${author.id}:${otherAuthor.id}:${production.id}`

        if (!uniqueRelations.has(relationKey)) {
          uniqueRelations.add(relationKey)
          allEdges.update({id: edgeId, label: `${(+edge.label || 0) + 1}`})
        }
      }
    }
  }

  for (let author of nodes.get()) {
    const others = lazy.filter(({id}) => id !== author.id, nodes.get())

    for (let other of others) {
      const edgeId = buildEdgeId(author, other)

      if (!allEdges.get(edgeId)) {
        allEdges.add({
          id: edgeId,
          from: author.id,
          to: other.id,
          label: '0',
          value: 0,
          // color: colors.edge,
        })
      }
    }
  }

  // prim

  const v = nodes.get()[0].id
  const N = new Set(nodes.map(({id}) => id))
  const T = new Set([v])
  const V = new Set(nodes.map(({id}) => id).filter(id => id !== v))

  while (!utils.sameSet(N, T)) {
    const edge = allEdges.get({
      filter ({from, to}) {
        return (T.has(from) && V.has(to)) ||
               (V.has(from) && T.has(to))
      }
    })
      .reduce((a, b) => +a.label < +b.label ? a : b)

    const {from, to, label} = edge
    const k = V.has(from) ? from : to
    T.add(k)
    V.delete(k)

    edges.add(edge)
  }

   network.setOptions({
     nodes: {
       shape: 'dot',
       size: 16
     },

    edges: {
      color: {
        inherit: true,
      },
    },

     physics: {
       forceAtlas2Based: {
         gravitationalConstant: -300,
         centralGravity: 0.005,
         springLength: 230,
         springConstant: 0.18
       },
       maxVelocity: 146,
       solver: 'forceAtlas2Based',
       timestep: 0.35,
       stabilization: {iterations: 0}
     }
   })
}

function thirdFace() {
  const state = store.data()

  getPeople(state)

  for (let production of state.productions.values()) {
    if (production.year < 2015) continue

    const authors = config.get('ppgccOnly')
      ? lazy.filter(
          author => author.isPpgcc,
          production.authors.values()
        )
      : production.authors.values()

    for (let author of authors) {
      const yearNodeId = `author:${author.id}:year:${production.year}`

      if (!nodes.get(yearNodeId)) {
        nodes.add({
          id: yearNodeId,
          label: production.year,
          color: colors.year,
        })

        edges.add({
          id: `${yearNodeId}:edge`,
          from: author.id,
          to: yearNodeId,
          arrows: 'to',
        })
      }

      const typeNodeId = `${yearNodeId}:type:${production.type}`

      if (!nodes.get(typeNodeId)) {
        nodes.add({
          id: typeNodeId,
          label: titleCase(production.type),
          color: colors.publicationType,
        })

        edges.add({
          id: `${typeNodeId}:edge`,
          from: yearNodeId,
          to: typeNodeId,
          arrows: 'to',
        })
      }

      incrementValue(nodes, nodes.get(author.id))
      incrementValue(nodes, nodes.get(typeNodeId))
      incrementLabel(edges, edges.get(`${typeNodeId}:edge`))
    }
  }

  function buildEdgeId({id: idA}, {id: idB}) {
    if (idA > idB) {
      return `edge:${idA}:${idB}`
    }
    return `edge:${idB}:${idA}`
  }

   network.setOptions({
     nodes: {
       shape: 'dot',
       size: 16
     },

    edges: {
      color: {
        inherit: true,
      },
    },

     physics: {
       forceAtlas2Based: {
         gravitationalConstant: -25,
         centralGravity: 0.005,
         springLength: 230,
         springConstant: 0.18
       },
       maxVelocity: 146,
       solver: 'forceAtlas2Based',
       timestep: 0.35,
       stabilization: {iterations: 0}
     }
   })
}

function getPeople(state) {
  const notRepeated = person =>
    !nodes.get(person.id)

  const qualifiedPeople = config.get('ppgccOnly')
    ? lazy.filter(
      person => person.isPpgcc && notRepeated(person),
      state.people.values()
    )
    : lazy.filter(notRepeated, state.people.values())

  const people =
    lazy.map(
      ({id, name}) => ({
        id,
        label: name,
        color: colors.person,
      }),
      qualifiedPeople
    )

  nodes.add(Array.from(people))
}

function incrementValue(dataset, entry) {
  const currentValue = entry.value || 0
  dataset.update({
    id: entry.id,
    value: currentValue + 1,
  })
}

function incrementLabel(dataset, entry) {
  const currentValue = +entry.label || 0
  dataset.update({
    id: entry.id,
    label: `${currentValue + 1}`,
  })
}

function prepareForDijkstra() {
  nodes.update(itensToResetForDijkstra.nodes.map(id => ({id, color: colors.person})))
  edges.update(itensToResetForDijkstra.edges.map(id => ({
    id,
    color: {
      color: colors.edge,
      highlight: colors.edge,
      hover: colors.edge,
    }
  })))

  dijkstraMode = true
  $dijkstraBtn.setAttribute('disabled', true)
  $dijkstraBtn.innerText = 'Select two different nodes'
}

function onClick(e) {
  if (dijkstraMode && e.nodes.length) {
    const [node] = e.nodes

    nodesForDijkstra.add(node)

    switch (nodesForDijkstra.size) {
      case 2:
        console.log({nodesForDijkstra});
        runDijkstra(...nodesForDijkstra.values())
        dijkstraMode = false
        $dijkstraBtn.innerText = 'Dijkstra'
        $dijkstraBtn.removeAttribute('disabled')
        nodesForDijkstra = new Set([])
        break

      case 1:
        $dijkstraBtn.innerText = 'Select one more node'
        break
    }
  }
}

function runDijkstra(from, to) {
  const adjacencies = nodes.getIds()
    .reduce((acc, id) => {
      acc[id] = {}
      return acc
    }, {})

  edges.forEach(({from, to, value}) => {
    adjacencies[from][to] = value
    adjacencies[to][from] = value
  })

  const route = new dijkstra()

  for (let id of Object.keys(adjacencies)) {
    route.addNode(id, adjacencies[id])
  }

  const path = route.path(from, to)

  const edgesToHighlight = path.slice(1)
    .map((node, i) => {
      const before = path[i]
      return buildEdgeId({id: node}, {id: before})
    })

  nodes.update(path.map(id => ({id, color: colors.dijkstra})))
  edges.update(edgesToHighlight.map(id => ({
    id,
    color: {
      color: colors.dijkstra,
      highlight: colors.dijkstra,
      hover: colors.dijkstra,
    }
  })))

  itensToResetForDijkstra = {
    nodes: path,
    edges: edgesToHighlight,
  }
}

function buildEdgeId({id: idA}, {id: idB}) {
  if (idA > idB) {
    return `edge:${idA}:${idB}`
  }
  return `edge:${idB}:${idA}`
}



