import JSZip from 'jszip'
import {parseString, Parser} from 'xml2js'
import promisify from 'pify'
import camelCase from 'camel-case'
import i from 'imlazy'

const xmlParser = new Parser({
  explicitRoot: false,
  tagNameProcessors: [camelCase],
  attrNameProcessors: [camelCase],
})

export async function parseCurriculumZip(file) {
  const zip = await JSZip.loadAsync(file)
  const curriculumZipped = zip.files['curriculo.xml']
  const curriculumXml = Array.from( // fuck ISO-8859-1
    i.map(
      String.fromCharCode,
      await curriculumZipped.async('array')
    )
  )
    .join('')

  return await promisify(xmlParser.parseString)(curriculumXml)
}

export function enumerate(it) {
  return i.zip(it, i.range(1, +Infinity))
}

export function sameSet (A, B) {
  return contains(A, B) && contains(B, A)
}

export function contains (A, B) {
  return Array.from(B.values()).every(v => A.has(v))
}



