const store = {
  people: new Map(),
  productions: new Map(),
}

export function data() {
  return store
}

export function addPerson(person) {
  const {id} = person

  const exists = getPerson(id)
  if (exists) return exists

  store.people.set(id, Object.assign({
    productions: new Set(),
  }, person))

  return getPerson(id)
}

export function addProduction(production) {
  const {id} = production

  const exists = getProduction(id)
  if (exists) return exists

  store.productions.set(id, Object.assign({
    authors: new Set(),
  }, production))

  return getProduction(id)
}

export function getPerson(id) {
  return store.people.get(id)
}

export function getProduction(id) {
  return store.productions.get(id)
}




