import {DataSet} from 'vis'
import * as ui from './ui'

const FIRST_FACE = 0
const LAST_FACE = 2

const state = {
  ppgccOnly: true,
  face: 0,
  options: {},
}

export function setFace(n) {
  const face = Math.max(FIRST_FACE, Math.min(LAST_FACE, n))

  if (face !== state.face) {
    ui.clearNetwork()
  }

  state.face = face
  ui.redraw()
}

export function get(key) {
  return state[key]
}




